import React, { useState } from 'react';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
  faChevronDown, 
  faVial, 
  faPalette, 
  faArrowRight, 
  faTimes, 
  faBullseye,
  faPencilAlt,
  faImage,
  faCheckCircle
} from '@fortawesome/free-solid-svg-icons';
import Section from '../components/common/Section';
import Button from '../components/common/Button';

const PageContainer = styled.main`
  overflow-x: hidden;
`;

const PageHeader = styled.div`
  text-align: center;
  margin-bottom: 3rem;
  
  h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
    background: ${props => props.theme.gradients.primary};
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  .subtitle {
    font-size: 1.5rem;
    opacity: 0.8;
    max-width: 800px;
    margin: 0 auto;
    line-height: 1.6;
  }
`;

const PortfolioDropdown = styled.div`
  margin-bottom: 2rem;
`;

const DropdownButton = styled.button`
  width: 100%;
  padding: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: ${props => props.theme.colors.cardBg};
  border: none;
  border-radius: 10px 10px ${props => props.isOpen ? '0 0' : '10px 10px'};
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  color: ${props => props.theme.colors.darkText};
  transition: all 0.3s ease;
  box-shadow: ${props => props.theme.shadows.medium};
  
  &:hover {
    background: rgba(0, 255, 136, 0.05);
  }
  
  svg {
    margin-right: 0.5rem;
    color: ${props => props.theme.colors.primary};
    
    &.dropdown-icon {
      margin-right: 0;
      margin-left: 0.5rem;
      transition: transform 0.3s ease;
      transform: ${props => props.isOpen ? 'rotate(180deg)' : 'rotate(0)'};
    }
  }
`;

const DropdownContent = styled(motion.div)`
  overflow: hidden;
  background: ${props => props.theme.colors.cardBg};
  border-radius: 0 0 10px 10px;
  box-shadow: ${props => props.theme.shadows.medium};
  padding: ${props => props.isOpen ? '1.5rem' : '0 1.5rem'};
`;

// Updated Design Grid for better aspect ratios
const DesignGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 1.5rem;
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    grid-template-columns: 1fr;
  }
`;

// Fixed aspect ratio for brand portfolio items (more square)
const BrandDesignItem = styled.div`
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: ${props => props.theme.shadows.small};
  cursor: pointer;
  aspect-ratio: 1 / 1;
  background-color: ${props => props.theme.colors.cardBgDark};
  
  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    transition: transform 0.5s ease;
    padding: 0.5rem;
  }
  
  &:hover {
    img {
      transform: scale(1.05);
    }
    
    .design-overlay {
      opacity: 1;
    }
  }
`;

// Updated aspect ratio for social media designs (more portrait)
const SocialDesignItem = styled.div`
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: ${props => props.theme.shadows.small};
  cursor: pointer;
  aspect-ratio: 9 / 16;
  background-color: ${props => props.theme.colors.cardBgDark};
  
  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    transition: transform 0.5s ease;
    padding: 0.5rem;
  }
  
  &:hover {
    img {
      transform: scale(1.05);
    }
    
    .design-overlay {
      opacity: 1;
    }
  }
`;

const DesignOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0.3) 60%, rgba(0,0,0,0) 100%);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 1.5rem;
  opacity: 0;
  transition: opacity 0.3s ease;
  
  h3 {
    color: white;
    margin-bottom: 0.5rem;
    font-size: 1.25rem;
  }
  
  p {
    color: rgba(255, 255, 255, 0.8);
    font-size: 0.9rem;
    margin: 0;
  }
`;

const ButtonContainer = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 2rem;
`;

// Modal Components
const ModalOverlay = styled(motion.div)`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: ${props => props.theme.zIndices.modal};
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
`;

const ModalContent = styled(motion.div)`
  max-width: 90%;
  max-height: 90%;
  position: relative;
  
  img {
    max-width: 100%;
    max-height: 80vh;
    object-fit: contain;
    border-radius: 8px;
  }
`;

const CloseButton = styled.button`
  position: absolute;
  top: -40px;
  right: 0;
  background: none;
  border: none;
  color: white;
  font-size: 2rem;
  cursor: pointer;
  z-index: ${props => props.theme.zIndices.modal + 1};
  transition: all 0.3s ease;
  
  &:hover {
    color: ${props => props.theme.colors.primary};
    transform: rotate(90deg);
  }
`;

const ImageCaption = styled.div`
  color: white;
  margin-top: 1rem;
  text-align: center;
  
  h3 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
  }
  
  p {
    font-size: 1rem;
    color: rgba(255, 255, 255, 0.8);
  }
`;

// Process Flow Section
const ProcessSection = styled.div`
  margin: 4rem 0;
`;

const ProcessTitle = styled.h2`
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 3rem;
  background: ${props => props.theme.gradients.primary};
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
`;

const ProcessSteps = styled.div`
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  position: relative;
  
  &:before {
    content: '';
    position: absolute;
    top: 3.5rem;
    left: 10%;
    width: 80%;
    height: 4px;
    background: ${props => props.theme.gradients.primary};
    z-index: 0;
  }
  
  @media (max-width: 1024px) {
    grid-template-columns: repeat(2, 1fr);
    
    &:before {
      display: none;
    }
  }
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    grid-template-columns: 1fr;
  }
`;

const ProcessStep = styled(motion.div)`
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  z-index: 1;
`;

const StepNumber = styled.div`
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background: ${props => props.theme.gradients.primary};
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
  box-shadow: ${props => props.theme.shadows.primary};
`;

const StepContent = styled.div`
  background: ${props => props.theme.colors.cardBg};
  border-radius: 15px;
  padding: 2rem;
  width: 100%;
  box-shadow: ${props => props.theme.shadows.medium};
  height: 100%;
  
  h3 {
    font-size: 1.3rem;
    margin-bottom: 1rem;
    color: ${props => props.theme.colors.primary};
  }
  
  p {
    font-size: 1rem;
    color: ${props => props.theme.colors.darkText};
    opacity: 0.8;
    line-height: 1.5;
  }
  
  svg {
    font-size: 2rem;
    margin-bottom: 1rem;
    color: ${props => props.theme.colors.primaryLight};
  }
`;

function Designing() {
  const [brandPortfolioOpen, setBrandPortfolioOpen] = useState(false);
  const [socialPortfolioOpen, setSocialPortfolioOpen] = useState(false);
  const [selectedImage, setSelectedImage] = useState(null);
  
  const toggleBrandPortfolio = () => {
    setBrandPortfolioOpen(!brandPortfolioOpen);
  };
  
  const toggleSocialPortfolio = () => {
    setSocialPortfolioOpen(!socialPortfolioOpen);
  };
  
  const openModal = (image) => {
    setSelectedImage(image);
    document.body.style.overflow = 'hidden';
  };
  
  const closeModal = () => {
    setSelectedImage(null);
    document.body.style.overflow = 'auto';
  };
  
  const brandPortfolioItems = [
    {
      src: "/assets/images/dd1.png",
      alt: "Brand Identity",
      title: "Jan Bima",
      description: "Logo & Stationery Design"
    },
    {
      src: "/assets/images/dd5.png",
      alt: "Brand Identity",
      title: "LODHA",
      description: "Industrial Park Map"
    },
    {
      src: "/assets/images/dd4.png",
      alt: "Brand Identity",
      title: "Asian Paint",
      description: "Paint Ezzy Bot"
    },
    {
      src: "/assets/images/dd6.png",
      alt: "Brand Identity",
      title: "Asian Paint",
      description: "Ezzy Bot Design"
    }
  ];
  
  const socialPortfolioItems = [
    {
      src: "/assets/images/d1.png",
      alt: "Social Media Design",
      title: "Brand Identity",
      description: "Logo & Stationery Design"
    },
    {
      src: "/assets/images/d2.png",
      alt: "Social Media Design",
      title: "Brand Identity",
      description: "Logo & Stationery Design"
    },
    {
      src: "/assets/images/d4.png",
      alt: "Social Media Design",
      title: "Brand Identity",
      description: "Logo & Stationery Design"
    },
    {
      src: "/assets/images/d6.png",
      alt: "Social Media Design",
      title: "Brand Identity",
      description: "Logo & Stationery Design"
    },
    {
      src: "/assets/images/d8.png",
      alt: "Social Media Design",
      title: "Brand Identity",
      description: "Logo & Stationery Design"
    },
    {
      src: "/assets/images/d3.png",
      alt: "Social Media Design",
      title: "Brand Identity",
      description: "Logo & Stationery Design"
    }
  ];
  
  // Design workflow steps
  const processSteps = [
    {
      number: 1,
      title: "Discovery",
      description: "We learn about your brand values, audience, and design goals",
      icon: faBullseye
    },
    {
      number: 2,
      title: "Concept Creation",
      description: "Develop initial design concepts based on your requirements",
      icon: faPencilAlt
    },
    {
      number: 3,
      title: "Refinement",
      description: "Refine and polish the chosen design concept with your feedback",
      icon: faImage
    },
    {
      number: 4,
      title: "Finalization",
      description: "Deliver final designs in all formats required for your needs",
      icon: faCheckCircle
    }
  ];

  const processVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: (i) => ({ 
      opacity: 1, 
      y: 0, 
      transition: { 
        delay: 0.2 + (i * 0.2), 
        duration: 0.5 
      } 
    })
  };
  
  return (
    <PageContainer>
      <Section 
        padding="large"
        fullWidth={false}
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.4 }}
        isFirstSection={true}
      >
        <PageHeader>
          <motion.h1
            initial={{ y: -50, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ delay: 0.1, duration: 0.4 }}
          >
            Creative Design Solutions
          </motion.h1>
          <motion.p
            className="subtitle"
            initial={{ y: -30, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ delay: 0.2, duration: 0.4 }}
          >
            Crafting visual identities that captivate and convert
          </motion.p>
        </PageHeader>

        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.3, duration: 0.5 }}
        >
          {/* Designer's Portfolio Dropdown */}
          <PortfolioDropdown>
            <DropdownButton isOpen={brandPortfolioOpen} onClick={toggleBrandPortfolio}>
              <div>
                <FontAwesomeIcon icon={faVial} /> Designer's Portfolio ( For Brands )
              </div>
              <FontAwesomeIcon icon={faChevronDown} className="dropdown-icon" />
            </DropdownButton>
            
            <DropdownContent
              initial={{ height: 0 }}
              animate={{ height: brandPortfolioOpen ? 'auto' : 0 }}
              transition={{ duration: 0.3 }}
              isOpen={brandPortfolioOpen}
            >
              <DesignGrid>
                {brandPortfolioItems.map((item, index) => (
                  <BrandDesignItem key={index} onClick={() => openModal(item)}>
                    <img src={item.src} alt={item.alt} />
                    <DesignOverlay className="design-overlay">
                      <h3>{item.title}</h3>
                      <p>{item.description}</p>
                    </DesignOverlay>
                  </BrandDesignItem>
                ))}
              </DesignGrid>
            </DropdownContent>
          </PortfolioDropdown>
          
          {/* Design Portfolio Dropdown */}
          <PortfolioDropdown>
            <DropdownButton isOpen={socialPortfolioOpen} onClick={toggleSocialPortfolio}>
              <div>
                <FontAwesomeIcon icon={faPalette} /> Design Portfolio ( For Socials )
              </div>
              <FontAwesomeIcon icon={faChevronDown} className="dropdown-icon" />
            </DropdownButton>
            
            <DropdownContent
              initial={{ height: 0 }}
              animate={{ height: socialPortfolioOpen ? 'auto' : 0 }}
              transition={{ duration: 0.3 }}
              isOpen={socialPortfolioOpen}
            >
              <DesignGrid>
                {socialPortfolioItems.map((item, index) => (
                  <SocialDesignItem key={index} onClick={() => openModal(item)}>
                    <img src={item.src} alt={item.alt} />
                    <DesignOverlay className="design-overlay">
                      <h3>{item.title}</h3>
                      <p>{item.description}</p>
                    </DesignOverlay>
                  </SocialDesignItem>
                ))}
              </DesignGrid>
            </DropdownContent>
          </PortfolioDropdown>
          
          {/* Add Design Process Section */}
          <ProcessSection>
            <ProcessTitle>Our Design Process</ProcessTitle>
            <ProcessSteps>
              {processSteps.map((step, index) => (
                <ProcessStep
                  key={index}
                  custom={index}
                  initial="hidden"
                  animate="visible"
                  variants={processVariants}
                >
                  <StepNumber>{step.number}</StepNumber>
                  <StepContent>
                    <FontAwesomeIcon icon={step.icon} />
                    <h3>{step.title}</h3>
                    <p>{step.description}</p>
                  </StepContent>
                </ProcessStep>
              ))}
            </ProcessSteps>
          </ProcessSection>
        </motion.div>
      </Section>
        
      <Section
        title="Want designs that leave an impact?"
        description="Let's work together to create visuals that effectively communicate your brand message and captivate your audience."
        background={props => props.theme.colors.cardBg}
        rounded={true}
        withShadow={true}
        padding="medium"
        containerSize="medium"
        initial={{ opacity: 0, y: 50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.4, duration: 0.5 }}
      >
        <ButtonContainer>
          <Button
            as="a"
            href="https://forms.gle/GSFowRYiJRvq2R7SA" 
            target="_blank"
            variant="primary"
            size="large"
            icon={faArrowRight}
            pill={true}
          >
            Get in Touch
          </Button>
        </ButtonContainer>
      </Section>
      
      <AnimatePresence>
        {selectedImage && (
          <ModalOverlay
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={closeModal}
          >
            <ModalContent
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.8, opacity: 0 }}
              transition={{ type: "spring", damping: 25 }}
              onClick={(e) => e.stopPropagation()}
            >
              <CloseButton onClick={closeModal}>
                <FontAwesomeIcon icon={faTimes} />
              </CloseButton>
              <img src={selectedImage.src} alt={selectedImage.alt} />
              <ImageCaption>
                <h3>{selectedImage.title}</h3>
                <p>{selectedImage.description}</p>
              </ImageCaption>
            </ModalContent>
          </ModalOverlay>
        )}
      </AnimatePresence>
    </PageContainer>
  );
}

export default Designing; 