import { createGlobalStyle } from 'styled-components';

const GlobalStyles = createGlobalStyle`
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0, 255, 136, 0.15);
  }

  html {
    scroll-behavior: smooth;
    height: -webkit-fill-available; /* Mobile viewport height fix */
  }

  body {
    font-family: ${props => props.theme.fonts.body};
    color: ${props => props.theme.colors.darkText};
    background-color: ${props => props.theme.colors.bodyBg};
    line-height: 1.6;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
    min-height: 100vh;
    min-height: -webkit-fill-available; /* Mobile viewport height fix */
    touch-action: manipulation; /* Disable double-tap zoom on mobile */
  }

  h1, h2, h3, h4, h5, h6 {
    font-family: ${props => props.theme.fonts.heading};
    margin-bottom: 1rem;
    font-weight: 600;
    line-height: 1.2;
    color: ${props => props.theme.colors.lightText};
  }

  h1 {
    font-size: ${props => props.theme.fontSizes.huge};
    
    @media (max-width: ${props => props.theme.breakpoints.mobile}) {
      font-size: ${props => props.theme.fontSizes.xxlarge};
    }
  }

  h2 {
    font-size: ${props => props.theme.fontSizes.xxlarge};
    
    @media (max-width: ${props => props.theme.breakpoints.mobile}) {
      font-size: ${props => props.theme.fontSizes.xlarge};
    }
  }

  h3 {
    font-size: ${props => props.theme.fontSizes.xlarge};
    
    @media (max-width: ${props => props.theme.breakpoints.mobile}) {
      font-size: ${props => props.theme.fontSizes.large};
    }
  }

  p {
    margin-bottom: 1rem;
    color: ${props => props.theme.colors.darkText};
  }

  a {
    color: ${props => props.theme.colors.primary};
    text-decoration: none;
    transition: all 0.3s ease;
    
    &:hover {
      color: ${props => props.theme.colors.primaryLight};
    }
  }

  img {
    max-width: 100%;
    height: auto;
    display: block;
  }

  button {
    cursor: pointer;
    font-family: ${props => props.theme.fonts.body};
    border: none;
    background: none;
    min-height: 44px; /* Minimum touch target size */
    min-width: 44px; /* Minimum touch target size */
    
    &:focus-visible {
      outline: 2px solid ${props => props.theme.colors.primary};
      outline-offset: 2px;
    }
    
    &:focus:not(:focus-visible) {
      outline: none;
    }
  }

  ul, ol {
    list-style-position: inside;
    margin-bottom: 1rem;
    color: ${props => props.theme.colors.darkText};
  }

  input, textarea, select {
    font-family: ${props => props.theme.fonts.body};
    font-size: ${props => props.theme.fontSizes.medium};
    background-color: ${props => props.theme.colors.cardBgDark};
    color: ${props => props.theme.colors.darkText};
    border: 1px solid ${props => props.theme.colors.tertiary};
    border-radius: ${props => props.theme.borderRadius.small};
    padding: 0.75rem 1rem;
    width: 100%;
    min-height: 48px; /* Better touch targets on mobile */
    
    &:focus {
      border-color: ${props => props.theme.colors.primary};
      outline: none;
      box-shadow: 0 0 0 2px rgba(0, 255, 136, 0.25);
    }
    
    @media (max-width: ${props => props.theme.breakpoints.mobile}) {
      font-size: 16px; /* Prevent iOS zoom on focus */
      padding: 1rem;
    }
  }
  
  textarea {
    resize: vertical;
    min-height: 100px;
  }
  
  select {
    appearance: none;
    background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%2300FF88%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.4-12.8z%22%2F%3E%3C%2Fsvg%3E");
    background-repeat: no-repeat;
    background-position: right 0.7rem top 50%;
    background-size: 0.65rem auto;
    padding-right: 2rem;
  }

  /* Form focus order indicator */
  *:focus {
    outline: none;
  }
  
  *:focus-visible {
    outline: 2px solid ${props => props.theme.colors.primary};
    outline-offset: 2px;
  }

  ::selection {
    background-color: ${props => props.theme.colors.primary};
    color: white;
  }

  /* Custom Animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes slideUp {
    from {
      transform: translateY(20px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }

  @keyframes pulse {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.05);
    }
    100% {
      transform: scale(1);
    }
  }

  /* Width for container elements */
  .container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 ${props => props.theme.spacing.medium};
    
    @media (max-width: ${props => props.theme.breakpoints.mobile}) {
      padding: 0 ${props => props.theme.spacing.small};
    }
  }
  
  /* Scrollbar styling */
  ::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    
    @media (max-width: ${props => props.theme.breakpoints.mobile}) {
      width: 6px;
      height: 6px;
    }
  }
  
  ::-webkit-scrollbar-track {
    background: ${props => props.theme.colors.bodyBg};
  }
  
  ::-webkit-scrollbar-thumb {
    background: ${props => props.theme.colors.tertiary};
    border-radius: 5px;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: ${props => props.theme.colors.primary};
  }
  
  /* Helper classes */
  .visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
  }
  
  /* Disable pull-to-refresh on mobile */
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    body {
      overscroll-behavior-y: contain;
    }
  }
  
  /* Fix 100vh issue on mobile */
  .full-height {
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
  }
  
  /* Enhance touch targets for clickable elements */
  .touchable {
    min-height: 44px;
    min-width: 44px;
  }
`;

export { GlobalStyles }; 