import React from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faVideo, faPalette, faGlobe, faHandshake, faArrowRight } from '@fortawesome/free-solid-svg-icons';

// Hero Section
const HeroSection = styled.section`
  height: 90vh;
  max-height: 800px;
  display: flex;
  align-items: center;
  padding: 0 2rem;
  background: linear-gradient(135deg, rgba(0, 255, 136, 0.1) 0%, rgba(0, 204, 255, 0.1) 100%);
  margin-top: 4rem;
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    height: auto;
    padding: 6rem 2rem 4rem;
  }
`;

const HeroContent = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
`;

const MainHeading = styled(motion.h1)`
  font-size: 3.5rem;
  margin-bottom: 1.5rem;
  background: ${props => props.theme.gradients.primary};
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    font-size: 2.5rem;
  }
`;

const SubHeading = styled(motion.p)`
  font-size: 1.5rem;
  margin-bottom: 2rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    font-size: 1.2rem;
  }
`;

const HeroButtons = styled(motion.div)`
  display: flex;
  justify-content: center;
  gap: 1rem;
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    flex-direction: column;
    align-items: center;
  }
`;

const PrimaryButton = styled(Link)`
  display: inline-block;
  padding: 1rem 2rem;
  background: ${props => props.theme.gradients.primary};
  color: white;
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 255, 136, 0.3);
  
  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 7px 20px rgba(0, 255, 136, 0.4);
  }
`;

const SecondaryButton = styled(Link)`
  display: inline-block;
  padding: 1rem 2rem;
  background: transparent;
  color: ${props => props.theme.colors.primary};
  border: 2px solid ${props => props.theme.colors.primary};
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
  
  &:hover {
    background: rgba(0, 255, 136, 0.1);
    transform: translateY(-3px);
  }
`;

// Services Section
const ServicesSection = styled.section`
  padding: 6rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
`;

const SectionTitle = styled.h2`
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 3rem;
  background: ${props => props.theme.gradients.primary};
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
`;

const ServiceGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.5rem;
  
  @media (max-width: ${props => props.theme.breakpoints.tablet}) {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
`;

// Enhanced Service Card
const EnhancedServiceCard = styled(motion.div)`
  background: ${props => props.theme.colors.cardBg};
  border-radius: 16px;
  overflow: hidden;
  position: relative;
  box-shadow: ${props => props.theme.shadows.medium};
  height: 100%;
  display: flex;
  flex-direction: column;
  transition: all 0.4s ease;
  
  &:hover {
    transform: translateY(-12px);
    box-shadow: ${props => props.theme.shadows.large};
    
    .service-icon {
      transform: scale(1.1);
    }
    
    .service-link svg {
      transform: translateX(5px);
    }
    
    .card-gradient {
      opacity: 1;
      height: 100%;
    }
  }
`;

const CardTop = styled.div`
  padding: 2.5rem 2rem 1.5rem;
  position: relative;
  z-index: 2;
`;

const CardBottom = styled.div`
  padding: 0 2rem 2.5rem;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  position: relative;
  z-index: 2;
`;

const ServiceIcon = styled.div`
  background: ${props => props.theme.gradients.primary};
  width: 80px;
  height: 80px;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.5rem;
  box-shadow: 0 8px 20px rgba(0, 255, 136, 0.3);
  transition: transform 0.3s ease;
  
  svg {
    font-size: 2.5rem;
    color: white;
  }
`;

const ServiceTitle = styled.h3`
  font-size: 1.75rem;
  margin-bottom: 1rem;
  color: ${props => props.theme.colors.lightText};
`;

const ServiceDescription = styled.p`
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
  color: ${props => props.theme.colors.darkText};
  flex-grow: 1;
`;

const ServiceLink = styled(Link)`
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 600;
  text-decoration: none;
  color: ${props => props.theme.colors.primary};
  margin-top: 1rem;
  transition: all 0.3s ease;
  
  svg {
    transition: transform 0.3s ease;
  }
`;

const CardGradient = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background: ${props => props.theme.gradients.primary};
  opacity: 0.8;
  transition: all 0.4s ease;
  z-index: 1;
`;

// CTA Section
const CTASection = styled.section`
  padding: 6rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
`;

const CTAText = styled(motion.p)`
  font-size: 1.5rem;
  margin-bottom: 2rem;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
`;

const CTAButton = styled(motion.a)`
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 2.5rem;
  font-size: 1.2rem;
  background: ${props => props.theme.gradients.primary};
  color: white;
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 255, 136, 0.3);
  
  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 7px 20px rgba(0, 255, 136, 0.4);
  }
  
  svg {
    transition: transform 0.3s ease;
  }
  
  &:hover svg {
    transform: translateX(5px);
  }
`;

function Home() {
  // Service cards data
  const services = [
    {
      title: "Video Editing",
      description: "Turn raw clips into viral-ready masterpieces. Professional video editing services for content creators, businesses, and events.",
      icon: faVideo,
      link: "/video-editing"
    },
    {
      title: "Designing",
      description: "Brands that dazzle, designs that convert. Creative design solutions including branding, social media graphics, and UI/UX design.",
      icon: faPalette,
      link: "/designing"
    },
    {
      title: "Website Support",
      description: "Websites that work as hard as you do. End-to-end web development services from design to deployment with ongoing maintenance.",
      icon: faGlobe,
      link: "/website-support"
    },
    {
      title: "Creator Outreach",
      description: "Bridge brands with creators, effortlessly. Connect brands with relevant content creators for authentic partnerships and campaigns.",
      icon: faHandshake,
      link: "/creator-outreach"
    }
  ];
  
  const cardVariants = {
    offscreen: { y: 50, opacity: 0 },
    onscreen: (i) => ({
      y: 0,
      opacity: 1,
      transition: { 
        type: "spring",
        bounce: 0.4,
        duration: 0.8,
        delay: i * 0.1
      }
    })
  };
  
  return (
    <>
      <HeroSection>
        <HeroContent>
          <MainHeading
            initial={{ y: -50, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.5 }}
          >
            Elevate Your Digital Presence
          </MainHeading>
          <SubHeading
            initial={{ y: -30, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.5, delay: 0.2 }}
          >
            Creative production services for businesses and content creators
          </SubHeading>
          <HeroButtons
            initial={{ y: -20, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.5, delay: 0.4 }}
          >
            <PrimaryButton to="/video-editing">Explore Services</PrimaryButton>
            <SecondaryButton href="mailto:contact@yotproductions.com" as="a" target="_blank">Contact Us</SecondaryButton>
          </HeroButtons>
        </HeroContent>
      </HeroSection>
      
      <ServicesSection>
        <SectionTitle>Our Services</SectionTitle>
        <ServiceGrid>
          {services.map((service, index) => (
            <EnhancedServiceCard
              key={index}
              custom={index}
              initial="offscreen"
              whileInView="onscreen"
              viewport={{ once: true, amount: 0.2 }}
              variants={cardVariants}
            >
              <CardGradient className="card-gradient" />
              <CardTop>
                <ServiceIcon className="service-icon">
                  <FontAwesomeIcon icon={service.icon} />
                </ServiceIcon>
                <ServiceTitle>{service.title}</ServiceTitle>
              </CardTop>
              <CardBottom>
                <ServiceDescription>{service.description}</ServiceDescription>
                <ServiceLink to={service.link} className="service-link">
                  Explore {service.title} <FontAwesomeIcon icon={faArrowRight} />
                </ServiceLink>
              </CardBottom>
            </EnhancedServiceCard>
          ))}
        </ServiceGrid>
      </ServicesSection>
      
      <CTASection>
        <SectionTitle>Ready to Get Started?</SectionTitle>
        <CTAText
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          viewport={{ once: true }}
          transition={{ duration: 0.5 }}
        >
          Let's discuss how we can help elevate your digital presence and connect with your audience.
        </CTAText>
        <CTAButton
          href="https://forms.gle/GSFowRYiJRvq2R7SA"
          target="_blank"
          rel="noopener noreferrer"
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.5, delay: 0.2 }}
        >
          Get in Touch <FontAwesomeIcon icon={faArrowRight} />
        </CTAButton>
      </CTASection>
    </>
  );
}

export default Home; 