import React from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faShieldAlt } from '@fortawesome/free-solid-svg-icons';

const PageContainer = styled.div`
  padding-top: 80px;
  max-width: 1000px;
  margin: 0 auto;
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    padding-top: 70px;
  }
`;

const PageHeader = styled(motion.div)`
  text-align: center;
  padding: 3rem 1.5rem;
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    padding: 2rem 1rem;
  }
`;

const MainTitle = styled.h1`
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  background: ${props => props.theme.gradients.primary};
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  line-height: 1.2;
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    font-size: 2rem;
  }
`;

const SubTitle = styled.p`
  font-size: 1.2rem;
  max-width: 800px;
  margin: 0 auto;
  opacity: 0.8;
`;

const ContentSection = styled(motion.section)`
  background: ${props => props.theme.colors.cardBg};
  border-radius: 15px;
  padding: 2.5rem;
  margin: 0 1.5rem 3rem;
  box-shadow: ${props => props.theme.shadows.medium};
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    padding: 1.5rem;
    margin: 0 1rem 2rem;
    border-radius: 12px;
  }
`;

const SectionTitle = styled.h2`
  font-size: 1.75rem;
  margin-bottom: 1.5rem;
  color: ${props => props.theme.colors.primary};
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    font-size: 1.5rem;
  }
`;

const SectionSubtitle = styled.h3`
  font-size: 1.4rem;
  margin: 1.5rem 0 1rem;
  color: ${props => props.theme.colors.lightText};
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    font-size: 1.2rem;
  }
`;

const Paragraph = styled.p`
  margin-bottom: 1rem;
  line-height: 1.6;
`;

const List = styled.ul`
  margin: 1rem 0 1.5rem;
  padding-left: 1rem;
  
  li {
    margin-bottom: 0.5rem;
    padding-left: 0.5rem;
  }
`;

const UpdatedDate = styled.p`
  font-style: italic;
  opacity: 0.6;
  margin-top: 2rem;
  text-align: right;
`;

const IconHeader = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.5rem;
  
  svg {
    font-size: 3rem;
    color: ${props => props.theme.colors.primary};
    margin-right: 1rem;
    
    @media (max-width: ${props => props.theme.breakpoints.mobile}) {
      font-size: 2.5rem;
    }
  }
`;

function PrivacyPolicy() {
  return (
    <PageContainer>
      <PageHeader
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <IconHeader>
          <FontAwesomeIcon icon={faShieldAlt} />
        </IconHeader>
        <MainTitle>Privacy Policy</MainTitle>
        <SubTitle>
          Your privacy is important to us. Here's how we collect, use, and protect your information.
        </SubTitle>
      </PageHeader>
      
      <ContentSection
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.2 }}
      >
        <SectionTitle>Introduction</SectionTitle>
        <Paragraph>
          At YOT Productions, we respect your privacy and are committed to protecting your personal data. 
          This privacy policy will inform you about how we look after your personal data when you visit 
          our website and tell you about your privacy rights and how the law protects you.
        </Paragraph>
        <Paragraph>
          This privacy policy applies to personal information we collect when you use our website, 
          services, or otherwise interact with us.
        </Paragraph>
        
        <SectionSubtitle>Information We Collect</SectionSubtitle>
        <Paragraph>
          We may collect, use, store, and transfer different kinds of personal data about you, including:
        </Paragraph>
        <List>
          <li>Identity Data: includes first name, last name, username or similar identifier.</li>
          <li>Contact Data: includes email address, telephone number, and physical address.</li>
          <li>Technical Data: includes internet protocol (IP) address, browser type and version, time zone setting, browser plug-in types and versions, operating system and platform.</li>
          <li>Usage Data: includes information about how you use our website and services.</li>
        </List>
        
        <SectionSubtitle>How We Use Your Information</SectionSubtitle>
        <Paragraph>
          We will only use your personal data when the law allows us to. Most commonly, we will use your personal data in the following circumstances:
        </Paragraph>
        <List>
          <li>To provide and maintain our services to you.</li>
          <li>To notify you about changes to our services.</li>
          <li>To allow you to participate in interactive features of our service when you choose to do so.</li>
          <li>To provide customer support.</li>
          <li>To gather analysis or valuable information so that we can improve our services.</li>
          <li>To monitor the usage of our services.</li>
          <li>To detect, prevent and address technical issues.</li>
        </List>
        
        <SectionSubtitle>Cookies</SectionSubtitle>
        <Paragraph>
          We use cookies and similar tracking technologies to track activity on our website and hold certain information.
          Cookies are files with a small amount of data which may include an anonymous unique identifier.
        </Paragraph>
        <Paragraph>
          You can instruct your browser to refuse all cookies or to indicate when a cookie is being sent. However, if you do not accept cookies, you may not be able to use some portions of our website.
        </Paragraph>
        
        <UpdatedDate>Last updated: March 10, 2025</UpdatedDate>
      </ContentSection>
      
      <ContentSection
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.3 }}
      >
        <SectionTitle>Data Security</SectionTitle>
        <Paragraph>
          We have implemented appropriate security measures to prevent your personal data from being accidentally lost, 
          used, or accessed in an unauthorized way, altered, or disclosed. We limit access to your personal data to those
          employees, agents, contractors, and other third parties who have a business need to know.
        </Paragraph>
        
        <SectionSubtitle>Third-Party Links</SectionSubtitle>
        <Paragraph>
          Our website may include links to third-party websites, plug-ins, and applications. Clicking on those links 
          or enabling those connections may allow third parties to collect or share data about you. We do not control 
          these third-party websites and are not responsible for their privacy statements.
        </Paragraph>
        
        <SectionSubtitle>Your Rights</SectionSubtitle>
        <Paragraph>
          Under certain circumstances, you have rights under data protection laws in relation to your personal data, including the right to:
        </Paragraph>
        <List>
          <li>Request access to your personal data.</li>
          <li>Request correction of your personal data.</li>
          <li>Request erasure of your personal data.</li>
          <li>Object to processing of your personal data.</li>
          <li>Request restriction of processing your personal data.</li>
          <li>Request transfer of your personal data.</li>
          <li>Right to withdraw consent.</li>
        </List>
        
        <SectionSubtitle>Contact Us</SectionSubtitle>
        <Paragraph>
          If you have any questions about this privacy policy or our privacy practices, please contact us at:
        </Paragraph>
        <Paragraph>
          Email: contact@yotproductions.com<br />
          Phone: +91 97572 71093
        </Paragraph>
      </ContentSection>
    </PageContainer>
  );
}

export default PrivacyPolicy; 