import React from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
  faChartLine, 
  faUsers, 
  faDollarSign, 
  faHandshake, 
  faArrowRight, 
  faUserPlus,
  faComments,
  faSearch,
  faChartBar
} from '@fortawesome/free-solid-svg-icons';
import Section from '../components/common/Section';
import Button from '../components/common/Button';

const PageContainer = styled.main`
  overflow-x: hidden;
`;

const PageHeader = styled.div`
  text-align: center;
  margin-bottom: 3rem;
  
  h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
    background: ${props => props.theme.gradients.primary};
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  .subtitle {
    font-size: 1.5rem;
    opacity: 0.8;
    max-width: 800px;
    margin: 0 auto;
    line-height: 1.6;
  }
`;

const BenefitsGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin: 3rem 0;
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
`;

const BenefitCard = styled(motion.div)`
  background: ${props => props.theme.colors.cardBg};
  border-radius: 15px;
  padding: 2.5rem 2rem;
  text-align: center;
  box-shadow: ${props => props.theme.shadows.medium};
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
  &:hover {
    transform: translateY(-10px);
    box-shadow: ${props => props.theme.shadows.large};
    
    svg {
      color: ${props => props.theme.colors.primary};
      transform: scale(1.1);
    }
  }
  
  svg {
    font-size: 3rem;
    margin-bottom: 1.5rem;
    color: ${props => props.theme.colors.primaryLight};
    transition: all 0.3s ease;
  }
  
  h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    color: ${props => props.theme.colors.primary};
  }
  
  p {
    font-size: 1.1rem;
    color: ${props => props.theme.colors.darkText};
    opacity: 0.8;
    line-height: 1.5;
  }
`;

const TrustSection = styled(motion.div)`
  background: ${props => props.theme.colors.cardBg};
  border-radius: 15px;
  padding: 3rem 2rem;
  margin-bottom: 4rem;
  text-align: center;
  box-shadow: ${props => props.theme.shadows.medium};
  
  svg {
    font-size: 3rem;
    margin-bottom: 1.5rem;
    color: ${props => props.theme.colors.primary};
  }
  
  h2 {
    font-size: 2rem;
    margin-bottom: 1.5rem;
    background: ${props => props.theme.gradients.primary};
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  p {
    font-size: 1.2rem;
    max-width: 800px;
    margin: 0 auto;
    opacity: 0.8;
    line-height: 1.6;
  }
`;

const ButtonGroup = styled.div`
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 2rem;
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
`;

// New process flow section
const ProcessSection = styled.div`
  margin: 4rem 0;
`;

const ProcessTitle = styled.h2`
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 3rem;
  background: ${props => props.theme.gradients.primary};
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
`;

const ProcessSteps = styled.div`
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  position: relative;
  
  &:before {
    content: '';
    position: absolute;
    top: 3.5rem;
    left: 10%;
    width: 80%;
    height: 4px;
    background: ${props => props.theme.gradients.primary};
    z-index: 0;
  }
  
  @media (max-width: 1024px) {
    grid-template-columns: repeat(2, 1fr);
    
    &:before {
      display: none;
    }
  }
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    grid-template-columns: 1fr;
  }
`;

const ProcessStep = styled(motion.div)`
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  z-index: 1;
`;

const StepNumber = styled.div`
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background: ${props => props.theme.gradients.primary};
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
  box-shadow: ${props => props.theme.shadows.primary};
`;

const StepContent = styled.div`
  background: ${props => props.theme.colors.cardBg};
  border-radius: 15px;
  padding: 2rem;
  width: 100%;
  box-shadow: ${props => props.theme.shadows.medium};
  height: 100%;
  
  h3 {
    font-size: 1.3rem;
    margin-bottom: 1rem;
    color: ${props => props.theme.colors.primary};
  }
  
  p {
    font-size: 1rem;
    color: ${props => props.theme.colors.darkText};
    opacity: 0.8;
    line-height: 1.5;
  }
  
  svg {
    font-size: 2rem;
    margin-bottom: 1rem;
    color: ${props => props.theme.colors.primaryLight};
  }
`;

const benefits = [
  {
    icon: faChartLine,
    title: "Boost Engagement",
    description: "Leverage creator authenticity to drive meaningful interactions"
  },
  {
    icon: faUsers,
    title: "Expand Reach",
    description: "Access established audiences through trusted creators"
  },
  {
    icon: faDollarSign,
    title: "Increase Conversions",
    description: "Turn creator influence into measurable sales growth"
  }
];

const processSteps = [
  {
    number: 1,
    title: "Identify Goals",
    description: "Define your brand objectives and target audience demographics",
    icon: faSearch
  },
  {
    number: 2,
    title: "Match Creators",
    description: "We pair you with creators who align with your brand values",
    icon: faUsers
  },
  {
    number: 3,
    title: "Collaborate",
    description: "Develop campaign briefs and coordinate content creation",
    icon: faComments
  },
  {
    number: 4,
    title: "Measure Results",
    description: "Track engagement, conversions, and ROI of creator campaigns",
    icon: faChartBar
  }
];

function CreatorOutreach() {
  const cardVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: (i) => ({ 
      opacity: 1, 
      y: 0, 
      transition: { 
        delay: 0.3 + (i * 0.1), 
        duration: 0.5 
      } 
    })
  };
  
  const processVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: (i) => ({ 
      opacity: 1, 
      y: 0, 
      transition: { 
        delay: 0.2 + (i * 0.2), 
        duration: 0.5 
      } 
    })
  };
  
  return (
    <PageContainer>
      <Section 
        padding="large"
        fullWidth={false}
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.4 }}
        isFirstSection={true}
      >
        <PageHeader>
          <motion.h1
            initial={{ y: -50, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ delay: 0.1, duration: 0.4 }}
          >
            Amplify Your Brand Through Creator Collaborations
          </motion.h1>
          <motion.p
            className="subtitle"
            initial={{ y: -30, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ delay: 0.2, duration: 0.4 }}
          >
            Bridge your brand with authentic voices that resonate with your target audience
          </motion.p>
        </PageHeader>

        <BenefitsGrid>
          {benefits.map((benefit, index) => (
            <BenefitCard
              key={index}
              custom={index}
              initial="hidden"
              animate="visible"
              variants={cardVariants}
            >
              <FontAwesomeIcon icon={benefit.icon} />
              <h3>{benefit.title}</h3>
              <p>{benefit.description}</p>
            </BenefitCard>
          ))}
        </BenefitsGrid>

        <TrustSection
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.8, duration: 0.5 }}
        >
          <FontAwesomeIcon icon={faHandshake} />
          <h2>Ready to elevate your brand's social presence?</h2>
          <p>Let's connect you with the perfect creators for your target audience</p>
        </TrustSection>
        
        <ProcessSection>
          <ProcessTitle>Our Approach</ProcessTitle>
          <ProcessSteps>
            {processSteps.map((step, index) => (
              <ProcessStep
                key={index}
                custom={index}
                initial="hidden"
                animate="visible"
                variants={processVariants}
              >
                <StepNumber>{step.number}</StepNumber>
                <StepContent>
                  <FontAwesomeIcon icon={step.icon} />
                  <h3>{step.title}</h3>
                  <p>{step.description}</p>
                </StepContent>
              </ProcessStep>
            ))}
          </ProcessSteps>
        </ProcessSection>
      </Section>
        
      <Section
        title="Enhance your brand presence on social media!"
        description="Get started today with our creator outreach services."
        background={props => props.theme.colors.cardBg}
        rounded={true}
        withShadow={true}
        padding="medium"
        containerSize="medium"
        initial={{ opacity: 0, y: 50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.4, duration: 0.5 }}
      >
        <ButtonGroup>
          <Button
            as="a"
            href="https://forms.gle/GSFowRYiJRvq2R7SA" 
            target="_blank"
            variant="primary"
            size="large"
            icon={faArrowRight}
            pill={true}
          >
            Get in Touch
          </Button>
          
          <Button
            as="a"
            href="https://forms.gle/rL78x3gcQ2P7q3CT6" 
            target="_blank"
            variant="secondary"
            size="large"
            icon={faUserPlus}
            pill={true}
          >
            Join as Creator
          </Button>
        </ButtonGroup>
      </Section>
    </PageContainer>
  );
}

export default CreatorOutreach; 