import React from 'react';
import styled, { css } from 'styled-components';
import { motion } from 'framer-motion';

// Base section styles
const SectionWrapper = styled(motion.section)`
  position: relative;
  padding: ${props => 
    props.padding === 'small' ? `${props.theme.spacing.large} 0` : 
    props.padding === 'medium' ? `${props.theme.spacing.xlarge} 0` : 
    props.padding === 'large' ? `${props.theme.spacing.xxlarge} 0` : 
    props.padding === 'none' ? '0' : 
    `${props.theme.spacing.xlarge} 0`
  };
  margin-top: ${props => props.isFirstSection ? '80px' : '0'};
  background-color: ${props => props.background || 'transparent'};
  overflow: ${props => props.overflow || 'visible'};
  z-index: ${props => props.zIndex || 1};
  
  ${props => props.gradient && css`
    background: ${props.gradient};
  `}
  
  ${props => props.fullWidth && css`
    width: 100%;
    max-width: 100%;
    padding-left: 0;
    padding-right: 0;
  `}
  
  ${props => props.withBorder && css`
    border-top: 1px solid ${props.theme.colors.tertiary};
    border-bottom: 1px solid ${props.theme.colors.tertiary};
  `}
  
  ${props => props.withShadow && css`
    box-shadow: ${props.theme.shadows.medium};
  `}
  
  ${props => props.rounded && css`
    border-radius: ${props.theme.borderRadius.large};
  `}
`;

const Container = styled.div`
  width: 100%;
  max-width: ${props => 
    props.size === 'small' ? '800px' : 
    props.size === 'medium' ? '1000px' : 
    props.size === 'large' ? '1200px' : 
    props.size === 'full' ? '100%' : 
    '1200px'
  };
  margin: 0 auto;
  padding: 0 ${props => props.theme.spacing.large};
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    padding: 0 ${props => props.theme.spacing.medium};
  }
`;

const SectionTitle = styled.h2`
  font-size: ${props => 
    props.size === 'small' ? props.theme.fontSizes.xlarge : 
    props.size === 'medium' ? props.theme.fontSizes.xxlarge : 
    props.size === 'large' ? props.theme.fontSizes.huge : 
    props.theme.fontSizes.xxlarge
  };
  margin-bottom: ${props => props.theme.spacing.large};
  text-align: ${props => props.align || 'center'};
  
  ${props => props.withGradient && css`
    background: ${props.theme.gradients.primary};
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  `}
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    font-size: ${props => 
      props.size === 'small' ? props.theme.fontSizes.large : 
      props.size === 'medium' ? props.theme.fontSizes.xlarge : 
      props.size === 'large' ? props.theme.fontSizes.xxlarge : 
      props.theme.fontSizes.xlarge
    };
    margin-bottom: ${props => props.theme.spacing.medium};
  }
`;

const SectionDescription = styled.p`
  font-size: ${props => props.theme.fontSizes.large};
  text-align: ${props => props.align || 'center'};
  margin-bottom: ${props => props.theme.spacing.xlarge};
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  opacity: 0.8;
  line-height: 1.6;
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    font-size: ${props => props.theme.fontSizes.medium};
    margin-bottom: ${props => props.theme.spacing.large};
  }
`;

const ContentWrapper = styled.div`
  margin-top: ${props => props.hasHeader ? props.theme.spacing.large : '0'};
`;

const Section = ({
  children,
  title,
  description,
  titleSize = 'medium',
  titleAlign = 'center',
  descriptionAlign = 'center',
  withGradientTitle = true,
  containerSize = 'large',
  padding = 'medium',
  background,
  gradient,
  fullWidth = false,
  withBorder = false,
  withShadow = false,
  rounded = false,
  overflow,
  zIndex,
  isFirstSection = false,
  
  // Animation props
  animate = true,
  initial = { opacity: 0, y: 30 },
  whileInView = { opacity: 1, y: 0 },
  viewport = { once: true, margin: "-100px" },
  transition = { duration: 0.6 },
  
  ...props
}) => {
  const sectionProps = {
    padding,
    background,
    gradient,
    fullWidth,
    withBorder,
    withShadow,
    rounded,
    overflow,
    zIndex,
    isFirstSection,
    ...(animate ? { 
      initial, 
      whileInView, 
      viewport, 
      transition 
    } : {}),
    ...props
  };
  
  const hasHeader = title || description;
  
  return (
    <SectionWrapper {...sectionProps}>
      <Container size={containerSize}>
        {title && (
          <SectionTitle 
            size={titleSize} 
            align={titleAlign}
            withGradient={withGradientTitle}
          >
            {title}
          </SectionTitle>
        )}
        
        {description && (
          <SectionDescription align={descriptionAlign}>
            {description}
          </SectionDescription>
        )}
        
        <ContentWrapper hasHeader={hasHeader}>
          {children}
        </ContentWrapper>
      </Container>
    </SectionWrapper>
  );
};

export default Section; 