import React from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faVideo, faPalette, faCode, faRocket } from '@fortawesome/free-solid-svg-icons';
import Section from '../components/common/Section';

const PageContainer = styled.main`
  overflow-x: hidden;
`;

const CareersHero = styled.div`
  text-align: center;
  padding: 4rem 2rem;
  
  h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
    background: ${props => props.theme.gradients.primary};
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  .subtitle {
    font-size: 1.5rem;
    opacity: 0.8;
    max-width: 800px;
    margin: 0 auto 3rem;
    line-height: 1.6;
  }
`;

const CareerButtons = styled.div`
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  max-width: 800px;
  margin: 0 auto;
`;

const CareerBtn = styled(motion.a)`
  position: relative;
  display: flex;
  align-items: center;
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  height: 100px;
  border-radius: 15px;
  padding: 2rem;
  text-decoration: none;
  overflow: hidden;
  transition: all 0.3s ease;
  box-shadow: ${props => props.theme.shadows.medium};
  z-index: 1;
  border: 1px solid rgba(255, 255, 255, 0.1);
  
  svg {
    font-size: 2.5rem;
    margin-right: 2rem;
    color: ${props => props.theme.colors.primary};
    transition: all 0.3s ease;
    z-index: 2;
  }
  
  &:hover {
    transform: translateX(10px);
    box-shadow: ${props => props.theme.shadows.large};
    
    svg {
      transform: scale(1.1);
    }
    
    .hover-effect {
      transform: translateX(0);
      opacity: 1;
    }
  }
`;

const HoverEffect = styled.span`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: ${props => props.gradient || props.theme.gradients.primary};
  opacity: 0;
  transform: translateX(-100%);
  transition: all 0.5s ease;
  z-index: 0;
`;

const CareerText = styled.span`
  font-size: 1.5rem;
  font-weight: 600;
  color: ${props => props.theme.colors.primary};
  z-index: 2;
  transition: all 0.3s ease;
  
  ${CareerBtn}:hover & {
    color: white;
  }
`;

function Careers() {
  const careers = [
    {
      title: "Video Editors",
      icon: faVideo,
      link: "https://forms.gle/nxonjtpSJvXeVJRs8",
      gradient: "linear-gradient(135deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%)"
    },
    {
      title: "Designers",
      icon: faPalette,
      link: "https://forms.gle/x7AzJ8wrrRrA1qJv5",
      gradient: "linear-gradient(135deg, #FF9A8B 0%, #FF6A88 55%, #FF99AC 100%)"
    },
    {
      title: "Web & Software Developers",
      icon: faCode,
      link: "https://forms.gle/xHcQhsnFFyGzw2s56",
      gradient: "linear-gradient(135deg, #13547A 0%, #80D0C7 100%)"
    },
    {
      title: "Creators",
      icon: faRocket,
      link: "https://forms.gle/fsCXfvzSK61Jvwax6",
      gradient: "linear-gradient(135deg, #FDFC47 0%, #24FE41 100%)"
    }
  ];

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: { 
      opacity: 1,
      transition: { 
        staggerChildren: 0.2
      }
    }
  };

  const itemVariants = {
    hidden: { opacity: 0, x: -50 },
    visible: { 
      opacity: 1, 
      x: 0,
      transition: { duration: 0.5 }
    }
  };

  return (
    <PageContainer>
      <Section 
        padding="large"
        fullWidth={false}
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.4 }}
        isFirstSection={true}
      >
        <CareersHero>
          <motion.h1
            initial={{ y: -50, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ delay: 0.1, duration: 0.4 }}
          >
            Join Our Creative Team
          </motion.h1>
          <motion.p
            className="subtitle"
            initial={{ y: -30, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ delay: 0.2, duration: 0.4 }}
          >
            Shape the future of digital content creation
          </motion.p>

          <CareerButtons
            as={motion.div}
            variants={containerVariants}
            initial="hidden"
            animate="visible"
          >
            {careers.map((career, index) => (
              <CareerBtn
                key={index}
                href={career.link}
                target="_blank"
                rel="noopener noreferrer"
                variants={itemVariants}
              >
                <HoverEffect gradient={career.gradient} className="hover-effect" />
                <FontAwesomeIcon icon={career.icon} />
                <CareerText>{career.title}</CareerText>
              </CareerBtn>
            ))}
          </CareerButtons>
        </CareersHero>
      </Section>
    </PageContainer>
  );
}

export default Careers; 