import React from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileContract } from '@fortawesome/free-solid-svg-icons';

const PageContainer = styled.div`
  padding-top: 80px;
  max-width: 1000px;
  margin: 0 auto;
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    padding-top: 70px;
  }
`;

const PageHeader = styled(motion.div)`
  text-align: center;
  padding: 3rem 1.5rem;
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    padding: 2rem 1rem;
  }
`;

const MainTitle = styled.h1`
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  background: ${props => props.theme.gradients.primary};
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  line-height: 1.2;
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    font-size: 2rem;
  }
`;

const SubTitle = styled.p`
  font-size: 1.2rem;
  max-width: 800px;
  margin: 0 auto;
  opacity: 0.8;
`;

const ContentSection = styled(motion.section)`
  background: ${props => props.theme.colors.cardBg};
  border-radius: 15px;
  padding: 2.5rem;
  margin: 0 1.5rem 3rem;
  box-shadow: ${props => props.theme.shadows.medium};
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    padding: 1.5rem;
    margin: 0 1rem 2rem;
    border-radius: 12px;
  }
`;

const SectionTitle = styled.h2`
  font-size: 1.75rem;
  margin-bottom: 1.5rem;
  color: ${props => props.theme.colors.primary};
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    font-size: 1.5rem;
  }
`;

const SectionSubtitle = styled.h3`
  font-size: 1.4rem;
  margin: 1.5rem 0 1rem;
  color: ${props => props.theme.colors.lightText};
  
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    font-size: 1.2rem;
  }
`;

const Paragraph = styled.p`
  margin-bottom: 1rem;
  line-height: 1.6;
`;

const List = styled.ul`
  margin: 1rem 0 1.5rem;
  padding-left: 1rem;
  
  li {
    margin-bottom: 0.5rem;
    padding-left: 0.5rem;
  }
`;

const UpdatedDate = styled.p`
  font-style: italic;
  opacity: 0.6;
  margin-top: 2rem;
  text-align: right;
`;

const IconHeader = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.5rem;
  
  svg {
    font-size: 3rem;
    color: ${props => props.theme.colors.primary};
    margin-right: 1rem;
    
    @media (max-width: ${props => props.theme.breakpoints.mobile}) {
      font-size: 2.5rem;
    }
  }
`;

function TermsOfService() {
  return (
    <PageContainer>
      <PageHeader
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <IconHeader>
          <FontAwesomeIcon icon={faFileContract} />
        </IconHeader>
        <MainTitle>Terms of Service</MainTitle>
        <SubTitle>
          Please read these terms carefully before using our services.
        </SubTitle>
      </PageHeader>
      
      <ContentSection
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.2 }}
      >
        <SectionTitle>Agreement to Terms</SectionTitle>
        <Paragraph>
          These Terms of Service govern your use of the website, services, and products provided 
          by YOT Productions. By accessing or using our services, you agree to be bound by these Terms.
          If you disagree with any part of the terms, you may not access our services.
        </Paragraph>
        
        <SectionSubtitle>Services Description</SectionSubtitle>
        <Paragraph>
          YOT Productions provides video editing, designing, web support, and creator outreach services 
          to clients. Our services include but are not limited to:
        </Paragraph>
        <List>
          <li>Video editing and post-production</li>
          <li>Graphic design and branding</li>
          <li>Website development and maintenance</li>
          <li>Social media content creation</li>
          <li>Influencer and creator partnerships</li>
        </List>
        
        <SectionSubtitle>User Accounts</SectionSubtitle>
        <Paragraph>
          When you create an account with us, you must provide accurate, complete, and up-to-date information. 
          You are responsible for safeguarding the password that you use to access our services and for any activities 
          or actions under your password.
        </Paragraph>
        <Paragraph>
          You agree not to disclose your password to any third party. You must notify us immediately upon becoming 
          aware of any breach of security or unauthorized use of your account.
        </Paragraph>
      </ContentSection>
      
      <ContentSection
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.3 }}
      >
        <SectionTitle>Intellectual Property</SectionTitle>
        <Paragraph>
          Our website and its original content, features, and functionality are owned by YOT Productions 
          and are protected by international copyright, trademark, patent, trade secret, and other intellectual 
          property or proprietary rights laws.
        </Paragraph>
        
        <SectionSubtitle>Client Content</SectionSubtitle>
        <Paragraph>
          If you submit content to us for our services (such as videos, images, or text), you retain ownership 
          of that content. By submitting content to us, you grant us a worldwide, non-exclusive, royalty-free license 
          to use, reproduce, adapt, publish, and distribute such content for the purpose of providing our services to you.
        </Paragraph>
        <Paragraph>
          You represent and warrant that you own or have the necessary licenses, rights, consents, and permissions 
          to use and authorize us to use all intellectual property rights in and to any content that you provide.
        </Paragraph>
        
        <SectionSubtitle>Portfolio Rights</SectionSubtitle>
        <Paragraph>
          Unless specified otherwise in a written agreement, YOT Productions reserves the right to showcase 
          work completed for clients in our portfolio, website, social media, or other promotional materials.
          If you wish to restrict this usage, please specify in writing before the commencement of services.
        </Paragraph>
      </ContentSection>
      
      <ContentSection
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.4 }}
      >
        <SectionTitle>Payment and Refunds</SectionTitle>
        <Paragraph>
          Payment terms will be established in individual service agreements or proposals.
          Generally, we require payment in advance or according to agreed-upon milestones.
        </Paragraph>
        
        <SectionSubtitle>Refund Policy</SectionSubtitle>
        <Paragraph>
          Refunds are provided at our discretion and will be detailed in individual service agreements.
          Generally, once work has commenced on a project, refunds may be limited to the portion of work not yet completed.
        </Paragraph>
        
        <SectionSubtitle>Termination</SectionSubtitle>
        <Paragraph>
          We may terminate or suspend access to our services immediately, without prior notice or liability, 
          for any reason whatsoever, including without limitation if you breach the Terms.
        </Paragraph>
        <Paragraph>
          All provisions of the Terms which by their nature should survive termination shall survive termination, 
          including, without limitation, ownership provisions, warranty disclaimers, indemnity, and limitations of liability.
        </Paragraph>
        
        <SectionSubtitle>Contact Us</SectionSubtitle>
        <Paragraph>
          If you have any questions about these Terms, please contact us at:
        </Paragraph>
        <Paragraph>
          Email: contact@yotproductions.com<br />
          Phone: +91 97572 71093
        </Paragraph>
        
        <UpdatedDate>Last updated: March 10, 2025</UpdatedDate>
      </ContentSection>
    </PageContainer>
  );
}

export default TermsOfService; 