import React from 'react';
import styled, { css } from 'styled-components';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

// Base button styles shared by all variants
const baseButtonStyles = css`
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  font-family: ${props => props.theme.fonts.body};
  font-weight: ${props => props.theme.fontWeights.semibold};
  font-size: ${props => props.size === 'large' ? props.theme.fontSizes.large : 
    props.size === 'small' ? props.theme.fontSizes.small : 
    props.theme.fontSizes.medium};
  line-height: 1;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  border-radius: ${props => props.pill ? props.theme.borderRadius.pill : props.theme.borderRadius.medium};
  padding: ${props => props.size === 'large' ? '1rem 2.5rem' : 
    props.size === 'small' ? '0.5rem 1rem' : 
    '0.75rem 1.5rem'};
  width: ${props => props.fullWidth ? '100%' : 'auto'};
  position: relative;
  overflow: hidden;
  min-height: ${props => props.size === 'large' ? '54px' : 
    props.size === 'small' ? '38px' : 
    '44px'};
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  
  /* Accessibility focus styles */
  &:focus-visible {
    outline: 2px solid white;
    outline-offset: 2px;
    box-shadow: 0 0 0 4px rgba(0, 255, 136, 0.5);
  }
  
  /* Mobile-specific adjustments */
  @media (max-width: ${props => props.theme.breakpoints.mobile}) {
    font-size: ${props => props.size === 'large' ? props.theme.fontSizes.large : 
      props.size === 'small' ? props.theme.fontSizes.small : 
      props.theme.fontSizes.medium};
    padding: ${props => props.size === 'large' ? '1.1rem 2.2rem' : 
      props.size === 'small' ? '0.6rem 1.2rem' : 
      '0.9rem 1.6rem'};
    min-width: ${props => props.size === 'large' ? '160px' : 
      props.size === 'small' ? '80px' : 
      '120px'};
  }
  
  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
  
  svg {
    transition: transform 0.3s ease;
    font-size: ${props => props.size === 'large' ? '1.2em' : '1em'};
  }
  
  &:hover:not(:disabled) svg {
    transform: ${props => props.iconPosition === 'right' ? 'translateX(4px)' : 
      props.iconPosition === 'left' ? 'translateX(-4px)' : 'scale(1.2)'};
  }
  
  /* Special handling for touch devices */
  @media (hover: none) {
    &:active:not(:disabled) {
      transform: scale(0.97);
    }
  }
`;

// Primary button styles
const PrimaryButton = styled.button`
  ${baseButtonStyles};
  background: ${props => props.theme.gradients.primary};
  color: ${props => props.theme.colors.lightText};
  box-shadow: ${props => props.theme.shadows.primary};
  
  &:hover:not(:disabled) {
    transform: translateY(-3px);
    box-shadow: ${props => props.theme.shadows.primaryHover};
  }
  
  &:active:not(:disabled) {
    transform: translateY(-1px);
    box-shadow: ${props => props.theme.shadows.primary};
  }
  
  /* Mobile touch feedback */
  @media (hover: none) {
    &:active:not(:disabled) {
      background: ${props => props.theme.gradients.hover};
    }
  }
`;

// Secondary button styles
const SecondaryButton = styled.button`
  ${baseButtonStyles};
  background: transparent;
  color: ${props => props.theme.colors.primary};
  border: 2px solid ${props => props.theme.colors.primary};
  
  &:hover:not(:disabled) {
    background: rgba(0, 255, 136, 0.1);
    transform: translateY(-3px);
  }
  
  &:active:not(:disabled) {
    transform: translateY(-1px);
    background: rgba(0, 255, 136, 0.2);
  }
  
  /* Mobile touch feedback */
  @media (hover: none) {
    &:active:not(:disabled) {
      background: rgba(0, 255, 136, 0.15);
    }
  }
`;

// Tertiary button styles (text only)
const TertiaryButton = styled.button`
  ${baseButtonStyles};
  background: transparent;
  color: ${props => props.theme.colors.primary};
  padding: ${props => props.size === 'large' ? '0.75rem 1.5rem' : 
    props.size === 'small' ? '0.25rem 0.5rem' : 
    '0.5rem 1rem'};
  
  &:hover:not(:disabled) {
    background: rgba(0, 255, 136, 0.1);
    transform: translateY(-3px);
  }
  
  &:active:not(:disabled) {
    transform: translateY(-1px);
    background: rgba(0, 255, 136, 0.15);
  }
  
  /* Mobile touch feedback */
  @media (hover: none) {
    &:active:not(:disabled) {
      background: rgba(0, 255, 136, 0.15);
    }
  }
`;

// Ripple animation for touch feedback
const ButtonRipple = styled.span`
  position: absolute;
  border-radius: 50%;
  transform: scale(0);
  animation: ripple 0.6s linear;
  background-color: rgba(255, 255, 255, 0.3);
  
  @keyframes ripple {
    to {
      transform: scale(4);
      opacity: 0;
    }
  }
`;

// Button component that chooses the right styled component based on variant
const Button = ({
  children,
  variant = 'primary',
  size = 'medium',
  icon,
  iconPosition = 'right',
  pill = false,
  as = 'button',
  to,
  href,
  fullWidth = false,
  onClick,
  type = 'button',
  disabled = false,
  ariaLabel,
  ...props
}) => {
  const [rippleCount, setRippleCount] = React.useState(0);
  const [rippleArray, setRippleArray] = React.useState([]);
  
  // Handle ripple effect
  const handleClick = (e) => {
    const rippleContainer = e.currentTarget.getBoundingClientRect();
    const size = rippleContainer.width > rippleContainer.height ? 
                 rippleContainer.width : rippleContainer.height;
    const x = e.clientX - rippleContainer.left - size / 2;
    const y = e.clientY - rippleContainer.top - size / 2;
    
    const newRipple = {
      key: rippleCount,
      size,
      x,
      y,
    };
    
    setRippleArray([...rippleArray, newRipple]);
    setRippleCount(rippleCount + 1);
    
    if (onClick) onClick(e);
  };
  
  // Remove ripple after animation completes
  React.useEffect(() => {
    let bounce = null;
    if (rippleArray.length > 0) {
      bounce = setTimeout(() => {
        setRippleArray([]);
        setRippleCount(0);
      }, 700);
    }
    
    return () => clearTimeout(bounce);
  }, [rippleArray.length]);
  
  // Choose the styled component based on variant
  const ButtonComponent = 
    variant === 'primary' ? PrimaryButton : 
    variant === 'secondary' ? SecondaryButton : 
    TertiaryButton;
  
  // Determine whether to use a Link, anchor, or button
  const buttonProps = {
    as: to ? Link : href ? 'a' : as,
    to, // For React Router Link
    href, // For anchor tags
    size,
    pill,
    fullWidth,
    onClick: handleClick,
    type,
    disabled,
    iconPosition,
    'aria-label': ariaLabel,
    ...props
  };
  
  return (
    <ButtonComponent {...buttonProps}>
      {icon && iconPosition === 'left' && <FontAwesomeIcon icon={icon} />}
      {children}
      {icon && iconPosition === 'right' && <FontAwesomeIcon icon={icon} />}
      
      {/* Render ripples */}
      {rippleArray.length > 0 && 
        rippleArray.map(ripple => (
          <ButtonRipple
            key={ripple.key}
            style={{
              top: ripple.y,
              left: ripple.x,
              width: ripple.size,
              height: ripple.size,
            }}
          />
        ))
      }
    </ButtonComponent>
  );
};

export default Button; 